import {Component, OnInit, AfterViewInit, PLATFORM_ID, Inject} from '@angular/core';
import {OnDestroy} from '@angular/core';
import {Router} from '@angular/router';
import {Headers, Response} from '@angular/http';
import {HttpService} from '../services/http-service';
import {PushNotificationsService} from 'angular2-notifications';
import {Config} from '../../config';
import {DataService} from '../_services';
import {AuthenticationService} from '../_services';
import swal from 'sweetalert2';
import { isPlatformBrowser } from '@angular/common';
@Component({
    selector: 'app-header',
    templateUrl: './header.component.html',
    styleUrls: ['./header.scss']
})
export class HeaderComponent implements OnInit, AfterViewInit {

    img;
    showlink :boolean = false;
    logi :boolean=true;

    notificationpage;
    public options = {
        position: ["top", "left"],
        timeOut: 0,
        lastOnBottom: true,
    };
    maxnotifications;
    currentUser;
    notification_class: any = 'header-alarm dropdown-toggle';
    notifications: any = {'notifications': [{msg: 'Welcome!', created_at: Date.now()}]};
    currentUserLoaded: boolean = false;
     profile_image: any;

    constructor(private router: Router, private obj:AuthenticationService, private _pushNotifications: PushNotificationsService, private http: HttpService, private data: DataService,  @Inject(PLATFORM_ID) private platformId: Object) {
        this.currentUser = JSON.parse(localStorage.getItem('currentUser'));
        // alert(this.currentUser);
        this.notificationpage = 1;
    }


  check_login() {
    if (isPlatformBrowser(this.platformId)) {
      if (localStorage.getItem('currentUser')) {
        return true;
      }
      else {
        return false;
      }
    }
  }
    logout() {
        localStorage.clear();
        localStorage.removeItem('currentUser');
        localStorage.removeItem('currentPassword');
        this.logoutsweetalert();
        this.logi=false
      };
      logoutsweetalert() {
    
        swal.fire({
          title: "influexpai<br> Thanks for visiting us",
          type: "success",
          showConfirmButton: false,
          timer: 2000,
        })
        this.showlink = false;
      }
    notify(body) {
        let options = { //set options
            body: body['msg'],
            icon: "data:image/png;base64," //adding an icon

        };

        let notify = this._pushNotifications.create('InfluExp', options).subscribe( //creates a notification
            res => {
                // this.alertService.success('Registration successful', true);
                // this.router.navigate(['/login']);
                if (res.event.type == 'click') {
                    if (body['msg'].includes('has completed')) {
                        if (body['searchV'] && body['searchV'] == 'v1') {
                            this.router.navigate(['/blogs/search/', body['searchword']])
                        }
                        else {
                            this.router.navigate(['/blogs/search/v2/', body['searchword']])
                        }
                    }
                    // notify.clickToClose=true;
                }
            },
            err => {
                alert(err)
                this._pushNotifications.requestPermission();

            }
        );

    }
    
    // find1(e) {

    //     this.router.navigate(['/search/results/', e])
    //     localStorage.setItem('searchQuery1', e);
    // }
    loadCurrentUser(img){
        this.currentUser.profile_image=img;


    }
    opennav() {
        document.getElementById("overlay").style.height="100%";
    }
    closenav() {
        document.getElementById("overlay").style.height="0";
    }
    ngOnInit() {
        if(localStorage.getItem('currentUser') != null){
            // alert(this.currentUser.username)
            this.loadprofilepic();
           
            this.showlink = true;
        }else {
            this.showlink = false;
        }
        this.data.currentUser.subscribe(data => this.currentUser = data)

        this.initializeNotifications(this.currentUser.username);
        this._pushNotifications.requestPermission();
        
        this.currentUserLoaded = true;
    }

    initializeNotifications(username) {
        this.http.get(Config.api + '/notifications/' + username + '/', null, 'full')
            .subscribe(res => {
                if (res.json().results) {
                    this.notifications['notifications'] = res.json().results;
                    this.maxnotifications = res.json().totalPages;
                }

            });

    }

    loadMoreNotifications() {
        if (this.notificationpage < this.maxnotifications) {
            this.notificationpage = this.notificationpage + 1;
            const currentUser = JSON.parse(localStorage.getItem('currentUser'));
            this.http.get(Config.api + '/notifications/' + currentUser.username + '/?page=' + this.notificationpage, null, 'small')
                .subscribe(res => {
                    this.maxnotifications = res.json().totalPages;
                    for (let not of res.json().results) {
                        this.notifications['notifications'].push(not);
                    }


                });
        }

    }
  

    ngAfterViewInit() {

        $(function () {
            $('a[href="#search').on('click', function (event) {
                event.preventDefault();
                $('#search').addClass('open');
                $('#search > form > input[type="search"]').focus();
            });

            $('#search, #search button.close').on('click keyup', function (event) {
                if (event.target == this || event.target.className == 'close' || event.keyCode == 27) {
                    $(this).removeClass('open');
                }
            });

            $('#show-hide-sidebar-toggle').on('click', function () {
                if (!$('body').hasClass('sidebar-hidden')) {
                    $('body').addClass('sidebar-hidden');
                } else {
                    $('body').removeClass('sidebar-hidden');
                }
            });

            // Left mobile menu
            $('.hamburger').click(function () {
                if ($('body').hasClass('menu-left-opened')) {
                    $(this).removeClass('is-active');
                    $('body').removeClass('menu-left-opened');
                    $('html').css('overflow', 'auto');
                } else {
                    $(this).addClass('is-active');
                    $('body').addClass('menu-left-opened');
                    $('html').css('overflow', 'hidden');
                }
            });

            $('.mobile-menu-left-overlay').click(function () {
                $('.hamburger').removeClass('is-active');
                $('body').removeClass('menu-left-opened');
                $('html').css('overflow', 'auto');
            });

            // Right mobile menu
            $('.site-header .burger-right').click(function () {
                if ($('body').hasClass('menu-right-opened')) {
                    $('body').removeClass('menu-right-opened');
                    $('html').css('overflow', 'auto');
                } else {
                    $('.hamburger').removeClass('is-active');
                    $('body').removeClass('menu-left-opened');
                    $('body').addClass('menu-right-opened');
                    $('html').css('overflow', 'hidden');
                }
            });

            $('.mobile-menu-right-overlay').click(function () {
                $('body').removeClass('menu-right-opened');
                $('html').css('overflow', 'auto');
            });
            //Do not include! This prevents the form from submitting for DEMO purposes only!
            // $('form').submit(function(event) {
            //   event.preventDefault();
            //   return false;
            // })
        });
    }


    search(search: string) {
        //{"text":"arts","username":"billubhai","token":"eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJ1c2VyX2lkIjoxLCJlbWFpbCI6InVtYXIuYmlsYWxAYnJhaW5wbG93LmNvbSIsInVzZXJuYW1lIjoidW1hcmJpbGFsIiwib3JpZ19pYXQiOjE1MDMzOTU1NTMsImV4cCI6MTUwMzM5NTg1M30.5xZYL_1HOoIiRwRlF1NTZ-lxQRwKLlXh_Yx9RTaMPCg", "reply_channel": "daphne.response.rOXStTDRJo!RjpFXOwgzm"}

        const replay_channel = localStorage.getItem('replay_channel');
        const currentUser = JSON.parse(localStorage.getItem('currentUser'));
        let headers = new Headers();
        // if (currentUser && currentUser.token) {
        //     headers = new Headers({ 'Authorization': 'JWT ' + currentUser.token });
        // }


        headers.append('Content-Type', 'application/json');

        return this.http.post(Config.api + '/blog/tasks/', JSON.stringify({
                token: currentUser.token,
                reply_channel: replay_channel,
                search: search,
                username: currentUser.username
            }),
            {headers: headers}).map((response: Response) => response.json());
    }

    startBRO(value) {
        this.search(value)
            .subscribe(
                data => {

                },
                error => {

                    alert('error')
                });
    }

    gotoNotifcation(n) {
        if (n['msg'].includes('has completed')) {
            if (n['searchV'] && n['searchV'] == 'v1') {
                this.router.navigate(['/blogs/search/', n['searchword']])
            }
            else {
                this.router.navigate(['/blogs/search/v2/', n['searchword']])
            }
        }


    }


    checkNot() {
        this.notification_class = 'header-alarm dropdown-toggle';
    }

//     find1(e) {
// alert(e)
//         this.router.navigate(['/search/results/', e])
//         localStorage.setItem('searchQuery1', e);
//     }

    find1(e) {

        this.router.navigate(['/search/results/', e])
        localStorage.setItem('searchQuery1', e);
    }


    loadprofilepic(){
        this.obj.get_profile_pic().subscribe(observer=>{

            this.profile_image= observer;
        })
    }
}
